import React from "react";
import "./styles.css";
import { useState } from "react";

export default function App() {
  const [match, setMatch] = useState(false);
  const [counter, setCounter] = useState(null);
  const calculate = (e) => {
    if (e.target.id === "guess") {
      setMatch(false);
    }

    for (let i = 1; i < 50; i++) {
      if (i * (i + 1) == e.target.value) {
        setMatch(true);
        setCounter(i);
      }
    }
  };

  return (
    <div className="App">
      <h1>Hello </h1>
      <input type="text" id="guess" onBlur={calculate} />
      <button name="submit" onClick={calculate}>
        Submit
      </button>
      <br />
      <br />
      <span style={{ color: match ? "green" : "red" }}>
        {match ? "Correct" : "Incorrect"}
      </span>
      {counter}
    </div>
  );
}
